import React from 'react';
import { makeStyles } from '@material-ui/core';
import bgimage from '../assets/awalmula.png'

const useStyles = makeStyles({
    appMain: {
        background: '../assets/awalmula.png',
        paddingLeft: '10px',
        paddingRight: '10px',
        justifyContent:"center",
        alignItems: "center:",

        width: '100%',
        height: '400px'
    }
})

function MainRender(props) {
    const classes = useStyles()
    const handleLogin = () => {
        props.history.push('/AssetDistribution');
    }
    
    return (
        <>
            <div className={classes.appMain}>
                Welcome to Thandhuran
                <br />
                <input type="button" value='Login Form' onClick={handleLogin} />
            </div>
        </>

    );
}

export default MainRender;