import React, {Component} from 'react'
import { Grid } from '@material-ui/core'
import { makeStyles } from '@material-ui/core';
//import Highcharts from 'highcharts';
//import HighchartsReact from 'highcharts-react-official';

import Detail from '../../components/Detail'
import DetailMaterial from '../../components/DetailMaterial'
//import MapBase from '../../components/Map/MapBase'

import GridPage from '../GridPage'

function createData(id, name, code, type, status) {
  return { id, name, code, type, status };
}

const rows = [
  createData('1', 'Gudang', 'inwrh0001','enclave','operational'),
  createData('2', 'Kantor', 'inoff0001','enclave','operational'),
  createData('3', 'Pabrik Kelapa Sawit', 'infac0001 ','enclave','operational'),
  createData('4', 'Pemukiman', 'inset0001', 'enclave', 'operational'),
  createData('5', 'Penampungan Air', 'inres0001','enclave','operational'),
];

const headCells = [
  { id: 'id', numeric: false, disablePadding: true, label: 'Sequence' },
  { id: 'name', numeric: false, disablePadding: true, label: 'Commodity' },
  { id: 'code', numeric: false, disablePadding: true, label: 'Code' },
  { id: 'type', numeric: false, disablePadding: true, label: 'Type' },
  { id: 'status', numeric: false, disablePadding: true, label: 'Status' },
];

const datatheme='Infrastructure';

const useStyles = makeStyles({
    appHeader: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      paddingTop: '5px',
    },
    appMaster: {
      paddingLeft: '10px',
      paddingRight: '10px',
      width: '100%',
      maxHeight: '500px',
    },
    appDetail: {
      paddingLeft: '10px',
      width: '100%',
      verticalAlign: 'top',
    }
})

function Infrastructure() {
  const classes = useStyles()
  const itemContent = [
    {
      id: 1,
      code: "kh7777zz",
      group: "Fleet Management",
      class: "transport",
      title: "Transport Truck KH 7777 ZZ",
      length: 3,
      lengthunit: "meter",
      width: 2.2,
      widthunit: "meter",
      height: 2.4,
      heightunit: "meter",
      brand: "Hino",
      productionyear: "2018",
      capacity: 10,
      capunit: "ton",
      dataacquisition: "06 Jun 2018",
      status: "",
    },
  ]
  const options = {
    chart: {
      type: 'spline'
    },
    title: {
      text: 'My chart'
    },
    series: [
      {
        data: [1, 2, 1, 4, 3, 6]
      }
    ]
  };
  return (
    <>
      <Grid container className={classes.appDetail}>
      <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme} />
          </Grid>
          <Grid className={classes.appHeader} sm={6}>
          <GridPage rows={rows} headCells={headCells} datatheme={datatheme + " Activities"} />
          </Grid>
      </Grid>
      <Grid container className={classes.appDetail}>
          <Grid className={classes.appHeader} item sm={6}>
          <Detail itemContent={itemContent} datatheme={datatheme + " Activity Detail"}/>
          </Grid>
          <Grid className={classes.appHeader} item sm={6}>
          <DetailMaterial itemContent={itemContent}/>
          </Grid>
      </Grid>
    </>
  )
}

export default Infrastructure;